.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: #e9fff4;
}

#startdiv1{
  display: flex;
  gap: 15%;
  margin-left: 25%;
  margin-top: 10%;
  
}

#startdiv1 form{
  border: 1px solid;
  padding: 20px;
  border-radius: 10%;
  text-align: center;
  width: 300px;
  background-color: #e8fff9;
  border-color: #1cafaf;
}

.card{
  border: 1px solid;
  padding: 5px;
  margin: 10px;
  width: 250px;

  text-align: center;
}

#RadioForPririority{
  margin-left: 1px;
  gap: 29%;
  display: flex;
}

.RadioInput{
  margin-left: 100px;
  padding: 100px;
}

#ListDivision{
  display: flex;
  margin: 5%;
  justify-content: space-around;
  row-gap: 10%;
}

#Urgent{
  border: solid rgb(121, 255, 239);
  border-radius: 7%;
  text-align: center;
  padding: 0.5%;
  background-color: #bef8e5;
}
#Normal{
  border: solid rgb(142, 255, 242);
  border-radius: 7%;
  text-align: center;
  padding: 0.5%;
  background-color: #d1feef;
}
#Trivial{
  border: solid rgb(160, 248, 238);
  border-radius: 7%;
  text-align: center;
  padding: 0.5%;
  background-color: rgb(237, 252, 244);
}


#Add{
  margin: 2% 30%;
  padding: 1% 5%;
  border-radius: 5%;
  border: solid #88f6d7;
  text-align: center;
  background-color: #ddfef1;
}

.Abort{
  background-color: red;
  margin: 1%;
  border-radius: 8%;
}
.Danger{
  background-color: skyblue;
  margin: 1%;
  border-radius: 8%;
}